import React, { useState } from "react";
import { Button, Spinner } from "reactstrap";
import { FiX } from "react-icons/fi";
import { useSelector, useDispatch } from "react-redux";

import { Formik, Form, FastField } from "formik";
import inputField from "../inputField/inputField";
import * as yup from "yup";

import { useHistory } from "react-router-dom";
import { toggleLoginForm, IslogIn } from "../../redux/Reducer/Login";
import { Checkout } from "../../redux/Reducer/Cart";

const axios = require("axios").default;
export default function Login() {
  const initialVaulues = { user: "", password: "" };
  const stateLogin = useSelector((state) => state.login);
  const dispatch = useDispatch();
  const history = useHistory();
  const validationSchema = yup.object().shape({
    user: yup.string().required("This field is required."),
    password: yup.string().required("This field is required."),
  });
  return (
    <Formik
      initialValues={initialVaulues}
      validationSchema={validationSchema}
      onSubmit={(value, actions) => {
        
        axios
          .post("https://leduchung.herokuapp.com/api/login", value)
          .then((res) => {
            alert(`Login success`);
           
            localStorage.setItem('token',JSON.stringify(res.data.token) )
            dispatch(IslogIn(res.data.user));
            axios.defaults.headers.common['Authorization'] = res.data.token;
           
            dispatch(toggleLoginForm());
            history.push("/");
            actions.setSubmitting(false);
          })
          .catch((error) => {
            if (error.response) {
              alert(error.response.data.error);
            }
            actions.setSubmitting(false)
          });
      }}
    >
      {(formikProps) => {
        const { values, errors, touched, isSubmitting } = formikProps;

        return (
         
          <div>
           {isSubmitting &&   <div className="loading"><Spinner  color="primary" /></div>}
            <div
              style={{
                textAlign: "center",
                fontSize: "2rem",
                color: "gold",
                padding: "1rem auto",
              }}
            >
              Login
            </div>
            
            <div
              className="login-closeBtn"
              onClick={() => dispatch(toggleLoginForm())}
            >
              <FiX />
            </div>
            <Form className="form">
              <FastField
                name="user"
                component={inputField}
                label="User name:"
                placeholder="UserName"
                type="text"
              ></FastField>
              <FastField
                name="password"
                component={inputField}
                label="Password:"
                placeholder="password"
                type="password"
              ></FastField>

              <Button type="submit">Login</Button>
            </Form>
          </div>
        );
      }}
    </Formik>
  );
}
